/* src/components/AddTaskForm.css */
.add-task-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.form-row {
  display: flex;
  flex-direction: column; /* Default to column for mobile */
  align-items: stretch; /* Make inputs full width in column */
  gap: 10px;
}

.task-input, .type-select, .xp-input {
  width: 100%; /* Full width for mobile */
  box-sizing: border-box; /* Include padding and border in element's total width and height */
}

.add-task-form label {
  font-weight: bold;
  color: #a0b0e0;
  margin-bottom: 5px; /* Space below label when stacked */
  text-align: left;
}

.add-button {
  align-self: flex-start;
  background-color: #5cb85c;
  border-color: #4cae4c;
}

.add-button:hover {
  background-color: #4cae4c;
  border-color: #398439;
  box-shadow: 0 0 10px #5cb85c;
}

/* Responsive adjustments for AddTaskForm */
@media (min-width: 500px) {
  .form-row {
    flex-direction: row; /* Row for larger screens */
    align-items: center;
    gap: 10px;
  }
  .add-task-form label {
    margin-bottom: 0; /* Reset margin */
    margin-right: 5px; /* Space between label and input */
  }
  .task-input {
    flex-grow: 1; /* Allow text input to take more space */
    width: auto; /* Reset width */
  }
  .type-select {
    width: auto; /* Reset width, let content decide or set a fixed width if preferred */
    min-width: 120px;
  }
  .xp-input {
    width: 70px; /* Reset width */
  }
}