/* src/index.css */
body {
  margin: 0;
  font-family: 'Roboto', 'Orbitron', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0a0f1e; /* Dark blue-black */
  color: #c0c0ff; /* Light blue/lavender text */
  overflow-x: hidden; /* Prevent horizontal scroll from glowing effects */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Scrollbar styling (optional, but fits the theme) */
::-webkit-scrollbar {
  width: 10px; /* Slightly wider */
}
::-webkit-scrollbar-track {
  background: #080c18; /* Even darker track */
  border-radius: 5px;
}
::-webkit-scrollbar-thumb {
  background: #2a3f78;
  border-radius: 5px;
  border: 2px solid #080c18; /* Creates a border around the thumb */
}
::-webkit-scrollbar-thumb:hover {
  background: #4a6fcc;
}